/*------------------------------------*/
/*  Menu Header Logo and Menu
/*------------------------------------*/
.navbar img {
  max-width: initial;
  min-width: initial;
}
.navbar a {
  font-weight: 400 !important;
}
@media (min-width: 768px) {
  .navbar a {
    font-size: 0.75rem !important;
    margin: 0 10px !important;
  }
  .navbar .logo img {
    max-width: 150px;
  }
  .navbar .search-icon {
    max-width: 40px !important;
  }
}
@media (min-width: 992px) {
  .navbar a {
    font-size: 1rem !important;
  }
}
@media (max-width: 768px) {
  .navbar a {
    overflow: hidden;
    width: 50px;
  }
  .navbar a img {
    min-width: 180px;
  }
  .navbar .menu-item {
    display: flex;
    min-width: 100%;
    height: 15vh;
    max-height: 90px;
    margin: 0px;
    padding: 0px;
  }
  .navbar .menu-item a {
    height: 100%;
    min-width: 100%;
    margin: 0px !important;
    justify-content: flex-start;
    display: flex !important;
    align-items: center;
    color: #b5b5b8;
  }
  .navbar.fixed-top {
    box-shadow: none !important;
  }
}
.navbar #navbarCollapse {
  justify-content: flex-end;
}
@media (max-width: 768px) {
  .navbar #navbarCollapse {
    min-width: 100%;
    height: 100vh;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex-basis: initial !important;
    flex-grow: initial !important;
  }
  .navbar #navbarCollapse .navbar-wrap {
    height: 100vh;
  }
  .navbar #navbarCollapse .container {
    display: flex;
    flex-direction: column;
    align-items: center !important;
    justify-content: center;
  }
}
.navbar #navbarCollapse .menu-primary {
  flex-grow: initial;
  flex-basis: initial;
  justify-content: space-evenly;
}
@media (max-width: 768px) {
  .navbar #navbarCollapse .menu-primary {
    height: 90vh;
  }
}
.navbar #navbarCollapse .menu-primary a {
  color: white;
  text-decoration: none;
  display: inline-block;
  position: relative;
  font-family: Inter;
  font-style: normal;
  font-size: 1rem;
  margin: 0 16px;
  font-weight: 700;
  line-height: 24px;
  white-space: nowrap;
}
@media (max-width: 768px) {
  .navbar #navbarCollapse .menu-primary a {
    color: #b5b5b8;
    font-size: 48px;
    font-family: Inter;
    font-weight: 700;
  }
  .navbar #navbarCollapse .menu-primary a:hover {
    color: white;
  }
}
.navbar #navbarCollapse .menu-primary a:hover:after {
  transform-origin: left top;
  transform: scale(1, 1);
}
.navbar #navbarCollapse .menu-primary a::after {
  position: absolute;
  left: 0;
  content: "";
  width: 100%;
  height: 2px;
  background: currentColor;
  bottom: -4px;
  transform: scale(0, 1);
  transition: transform 0.3s;
  transform-origin: right top;
}
.navbar.fixed-top {
  top: 0;
  position: fixed;
  animation-name: navbar;
  animation-duration: 0.5s;
  box-shadow: 0 5px 0 rgba(0, 0, 0, 0.1);
  background-color: black;
}

/*------------------------------------*/
/*  smooth animation
/*------------------------------------*/
@keyframes navbar {
  from {
    top: -20px;
    opacity: 0.5;
  }
  to {
    top: 0px;
    opacity: 1;
  }
}
