#searchform {
  display: flex;
  align-items: center;
  color: #fff;
}
#searchform input {
  border: 0;
  background: 0 0;
  padding: 0;
  outline: none;
  text-align: left;
  display: flex;
  align-items: center;
  color: #fff;
  transition: width 1s ease-in-out;
  width: 0;
}
#searchform input.extend {
  width: 140px;
}
@media (min-width: 768px) {
  #searchform input.extend {
    width: 100px;
  }
}

